form#createContentForm {
    .editor-wrapper {
        border: 1px solid #ced4da;
        border-radius: 0.5rem;
        min-height: 300px;
        .x-toolbar {
            border-radius: 0.5rem 0.5rem 0rem 0rem;
            border: 0px;
            border-bottom: 1px solid #ced4da;
            .rdw-option-wrapper {
                padding: .375rem .75rem !important;
                height: auto !important;
                min-width: auto !important;
                border-radius: .25rem !important;
            }
        }
        .editing-preview-area {
            padding: 0px 8px;
        }
    }
    #tablet {
        background: url("../../../images/tablet_campaign.png") no-repeat;
        width: 90vh;
        height: 70vh;
        background-size: contain;
        background-position: center;
        margin: 0px auto;
        padding: 5vh 7vh;
        margin-bottom: 20px;
        p.uploadDescription {
            width: 100%;
            margin-top: 20vh;
        }
    }
}

.ChannelCard {
    width: 250px;
    height: 300px;
    padding: 5px;
    margin-bottom: 30px;
    img {
        object-fit: cover;
        width: 238px;
        height: 238px;
    }
}
    